import { Location } from '@angular/common';
import { Router } from '@angular/router';
import { SafeUrl } from '@angular/platform-browser';
import { Store } from '@ngxs/store';
import { Camera } from '@ionic-native/camera/ngx';

import { UtilsService } from '../@core/utils.service';
import { BasePage } from './base.page';
import { Utils } from '../@global/utils';
import { InformService } from '../@core/inform.service';

export class ProfileImageEditorBasePage extends BasePage {
  public profileImage: SafeUrl;
  public imageOriginalPath: string;

  constructor(
    location: Location,
    router: Router,
    store: Store,
    utilsService: UtilsService,
    protected camera: Camera,
    protected informService: InformService
  ) {
    super(location, router, store, utilsService);
  }

  protected pickImageGallery = () => {
    this.pickImage(this.camera.PictureSourceType.PHOTOLIBRARY);
  };

  protected pickImageCamera = () => {
    this.pickImage(this.camera.PictureSourceType.CAMERA);
  };

  protected async pickImage(sourceType: number): Promise<void> {
    try {
      const imageData = await this.utilsService.getMediaFromCameraPlugin(sourceType, this.camera.MediaType.PICTURE);

      const { path, filename } = Utils.getPathAndFilenameFromUrl(imageData.imageOriginalPath);
      const file = await this.utilsService.getFileFromSystemPath(path, filename);

      if (!Utils.isImageMediaType(file.type)) {
        this.informService.showWarning('createPost.wrongImageType');
        return;
      }

      this.imageOriginalPath = imageData.imageOriginalPath;
      this.profileImage = imageData.imageSource;
    } catch (error) {
      console.error('[ProfileImageEditorBasePage pickImage] error taking a picture');
      console.error(error);

      const capturedError = new Error('Error in pickImage: ' + error);
      capturedError.name = '[ProfileImageEditorBasePage error]';
    }
  }
}
