import { Component, OnDestroy, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Location } from '@angular/common';
import { Store } from '@ngxs/store';
import { UtilsService } from '../../@core/utils.service';
import { StorageService } from '../../@core/storage.service';
import { NavigationExtrasService } from '../../@core/navigation-extras.service';
import { SignupBasePage } from '../signup-base.page';
import { SignupInfoModel } from '../signup-info.model';
import { Camera } from '@ionic-native/camera/ngx';
import { ActionSheetController, Platform } from '@ionic/angular';
import { Constants } from '../../@global/constants';
import { UploadProfileImage } from '../../@store/user/user.actions';
import { Utils } from '../../@global/utils';
import { InformService } from '../../@core/inform.service';
import { UserState } from '../../@store/user/user.state';
import { GoogleAnalyticsService } from '../../@core/google-analytics.service';

const PAGE_POSITION = 4;

@Component({
  selector: 'app-profile-image',
  templateUrl: './profile-image.page.html',
  styleUrls: ['./profile-image.page.scss'],
})
export class ProfileImagePage extends SignupBasePage implements OnInit, OnDestroy {
  public userData: SignupInfoModel;
  public noProfilePhotoPath = Constants.NO_PROFILE_IMAGE_PATH;

  constructor(
    router: Router,
    location: Location,
    store: Store,
    utilsService: UtilsService,
    camera: Camera,
    informService: InformService,
    storage: StorageService,
    navigationExtrasService: NavigationExtrasService,
    googleAnalyticsService: GoogleAnalyticsService,
    public actionSheetController: ActionSheetController,
    private platform: Platform
  ) {
    super(
      location,
      router,
      store,
      utilsService,
      camera,
      informService,
      storage,
      navigationExtrasService,
      googleAnalyticsService
    );
  }

  async ngOnInit() {
    // override showBackButton
    this.showBackButton = this.checkGoBack();

    // recover previous pages user info
    this.userData = await this.storage.getSignupData();
  }

  public async saveProfileImage() {
    try {
      if (this.platform.is('cordova')) {
        await this.uploadImageToBucket();
        await this.updateSignupFlowInfo(
          PAGE_POSITION,
          'photo',
          this.store.selectSnapshot(UserState.getcurrentUploadedPhotoKey)
        );
      }
      this.skip();
    } catch (error) {
      console.error(error);
    }
  }

  public skip() {
    // go next page
    this.goNextFlowPage(PAGE_POSITION);
  }

  private removePhoto = () => {
    this.profileImage = null;
  };

  public async selectImageFrom() {
    const buttons = [
      { text: 'cameraOptions.gallery', handler: this.pickImageGallery },
      { text: 'cameraOptions.camera', handler: this.pickImageCamera },
      { text: 'buttons.cancel' },
    ];

    await this.utilsService.showActionSheet('cameraOptions.actionSheetTitle', buttons);
  }

  public async showEditOptions() {
    const buttons = [
      { text: 'cameraOptions.choosePhoto', handler: this.pickImageGallery },
      { text: 'cameraOptions.camera', handler: this.pickImageCamera },
      { text: 'cameraOptions.deletePhoto', handler: this.removePhoto },
      { text: 'buttons.cancel' },
    ];

    await this.utilsService.showActionSheet('cameraOptions.actionSheetEditTitle', buttons);
  }

  private async uploadImageToBucket(): Promise<void> {
    const { path, filename } = Utils.getPathAndFilenameFromUrl(this.imageOriginalPath);
    const file = await this.utilsService.getFileFromSystemPath(path, filename);
    const filenameToUpload = Constants.PROFILE_FOLDER + '/' + Utils.generateFileUniqueName(file);

    if (!Utils.isMediaValidSize(file)) {
      this.informService.showWarning('createPost.invalidFileSize');
      throw new Error('invalidFileSize');
    }

    await this.store
      .dispatch(new UploadProfileImage({ filename: filenameToUpload, file, type: file.type }))
      .toPromise();
  }

  ngOnDestroy() {}
}
